.section-about{
	background-color: $color-grey-dark-1;
	padding: 25rem 0;
	// due to polygraph(75vh right side) which is used to style heading picture(95vh), section below it has to be 
	// brought up (to style triangle that is below the picture) 
	margin-top: -20vh;

	@include respond(tab-port){
		padding: 20rem 0;
	}
}

.section-features {
	padding: 20rem 0;
	background-image: linear-gradient(
		to right bottom,
		rgba($color-primary-dark, 0.8),
		rgba($color-primary-light, 0.8)),
	url(../img/nat-4.jpg);
	background-size: cover;

	transform: skewY(-7deg);
	margin-top: -10rem;

	// we selecting all direct children to remove the effect of the skew from feature-box
	& > * {
		transform: skewY(7deg);
	}
	@include respond(tab-port){
		padding: 10rem 0;
	}
}


.section-tours {
	background-color: $color-grey-dark-1;
	padding: 25rem 0 15rem 0;
	// due to polyraph(75vh right side) used to style heading picture(95vh), section below it has to be 
	// brought up to style triangle that is below the picture 
	margin-top: -10rem;

	@include respond(tab-port){
		padding: 20rem 0 10rem 0;
	}
}

.section-stories {
	padding: 15rem 0;
// position relative for video
	position: relative;
	
	@include respond(tab-port){
		padding: 10rem 0;
	}
}

.section-book {
	padding: 15rem 0;
	background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
	@include respond(tab-port){
		padding: 10rem 0;
	}
}

.book {
	background-image: linear-gradient(105deg,
									  rgba($color-white, .9) 0%,
									  rgba($color-white, .9) 50%,									  
	  								transparent 50%), 
	  								  url('../img/nat-10.jpg');
	background-size: cover;
	border-radius: 3px;
	box-shadow: 0 1.5rem 4rem rgba($color-black, .5);

	@include respond(tab-land){
		background-image: linear-gradient(105deg,
		rgba($color-white, .9) 0%,
		rgba($color-white, .9) 65%,									  
		transparent 65%), 
		  url('../img/nat-10.jpg');
		  background-size: cover;
	}

	@include respond(tab-port){
		background-image: linear-gradient(to right,
		rgba($color-white, .9) 0%,
		rgba($color-white, .9) 100%),									  
		  url('../img/nat-10.jpg');
		  background-size: cover;
	}

	&__form {
		width: 50%;
		padding: 6rem;

		@include respond(tab-land){
			width: 65%;
		}
		@include respond(tab-port){
			width: 100%;
		}
	}
}