.header {
	background-image: linear-gradient(
		to right bottom,
		 rgba($color-primary-light, 0.8),
		 rgba($color-primary-dark, 0.8)),
		 url(../img/hero-small.jpg);
	height: 95vh;
	background-size: cover;
	background-position: top;
	/* clip-path: circle(50%) */
	clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
	position: relative;

	@media (min-resolution: 192dpi) and (min-width: 37.3em),
			(-webkit-min-device-pixel-ratio:2) and (min-width: 37.3em),
			(min-width: 125em){
		background-image: linear-gradient(
		to right bottom,
		 rgba($color-primary-light, 0.8),
		 rgba($color-primary-dark, 0.8)),
		 url(../img/hero.jpg);
	height: 95vh;
	}

	@media (min-width: 2000px){
		background-image: linear-gradient(
		to right bottom,
		 rgba($color-primary-light, 0.8),
		 rgba($color-primary-dark, 0.8)),
		 url(../img/hero.jpg);
	height: 95vh;
	}

	@include respond(phone){
		clip-path: polygon(0 0, 100% 0, 100% 85vh, 0 100%);	
	}

	&__logo-box {
		position: absolute;
		top: 4rem;
		left: 4rem;
	}
	
	&__logo {
		height: 3.5rem;
	}
	
	&__text-box {
		position: absolute;
		top: 40%;
		left: 50%;
		transform: translate(-50%, -50%);
		/* wysrodkowanie buttona ktory jest linkiem */
		text-align: center;
		/* position: relative; */
	}
}


