.form {
	&__group:not(:last-child) {
		margin-bottom: 2rem;
	}
	
	&__input {
		font-size: 1.5rem;
		// input styles dont hinerit by default, so force inherit
		font-family: inherit;
		color: inherit;
		padding: 1.5rem 2rem;
		border-radius: 2px;
		background-color: rgba($color-white, 0.5);
		border: none;
		// we added 3px border in focus, so to avoid 'jumping' of the form we specify here additional transparent border
		border-bottom: 3px solid transparent;
		width: 90%;
		display: block;
		transition: all .3s;

		@include respond(tab-port){
			width: 100%;
		}

		&:focus {
			outline: none;
			// for accessibility reasons form focused forms should always have some visibility features (so we cant just remove outline)
			box-shadow: 0 1rem 2rem rgba($color-black, 0.1);
			border-bottom: 3px solid $color-primary;
		}
		// changes color of bottom border where email or name are wrong
		&:focus:invalid{
		border-bottom: 3px solid $color-secondary-dark;
			
		}
		&::-webkit-input-placeholder {
				color: $color-grey-dark-2;
		}
	}


	&__label {
		font-size: 1.2rem;
		font-weight: 700;
		margin-left: 2rem;
		margin-top: .7rem;
		display: block;
		transition: all .3s;
	}
// + Selects all elements that are placed immediately after elements
	&__input:placeholder-shown + &__label {
		opacity: 0;
		visibility: hidden;
		transform: translateY(1rem);
	}

	&__radio-group {
		width: 49%;
		display: inline-block;

		@include respond(tab-port){
			width: 100%;
			margin-bottom: 2rem;
		}
	}

	&__radio-input {
		display: none;
	}

	&__radio-label {
		font-size: $default-font-size;
		cursor: pointer;
		position: relative;
		padding-left: 4.5rem;
	}
	&__radio-button {
		height: 3rem;
		width: 3rem;
		border: 5px solid $color-primary;
		border-radius: 50%;
		display: inline-block;
		position: absolute;
		left: 0;
		top: -.4rem;

		&::after{
			content:'';
			display: block;
			height: 1.3rem;
			width: 1.3rem;
			border-radius: 50%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: $color-primary;
			opacity: 0;
			transition: opacity .2s;		
		}
	}
	&__radio-input:checked ~ &__radio-label &__radio-button::after {
		opacity: 1;
	}
		
}