.navigation {
	&__checkbox {
		display: none;
	}
	&__button {
		background-color: $color-white;
		height: 7rem;
		width: 7rem;
		position: fixed;
		top: 6rem;
		right: 6rem;
		border-radius: 50%;
		z-index: 2000;
		box-shadow: 0 1rem 3rem rgba($color-black, .1);
		cursor: pointer;

		@include respond(tab-port){
			top: 3rem;
			right: 3rem;
		}
		//wycentrowanie kresek w buttonie
		text-align: center;
	}
	&__background {
		height: 6rem;
		width: 6rem;
		border-radius: 50%;
		position: fixed;
		top: 6.5rem;
		right: 6.5rem;
		background-image: radial-gradient($color-primary-light, $color-primary-dark);
		z-index: 1000;
		transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);

		@include respond(tab-port){
			top: 3.8rem;
			right: 3.8rem;
		}
		@include respond(phone){
			top: 3.2rem;
			right: 3.2rem;
		}

	}
	&__nav {
		height: 100vh;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1500;
//	making links invisible 
		opacity: 0;
		width: 0;

		transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
	}
	&__list {
		@include centerIt;
		list-style: none;
		text-align: center;
		width: 100%;
	}
	&__item {
		margin: 1rem;
		
	}
	&__link {
		&:link,
		&:visited{
		display: inline-block;
		font-size: 3rem;
		font-weight: 300;
		padding: 1rem 2rem;
		color: $color-white;
		text-transform: uppercase;
		text-decoration: none;
		background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white 50%);
		background-size: 230%;
		transition: all .4s;

		span {
			margin-right: 1.5rem;
			display: inline-block;
		}
		}
		&:hover,
		&:active{
			background-position: 100%;
			color: $color-primary;
			transform: translateX(1rem);
		}
	}

	// functionality
	&__checkbox:checked ~ &__background {
		transform: scale(80);
	}
	&__checkbox:checked ~ &__nav {
		opacity: 1;
		width: 100%;
	}

	// icon
	&__icon {
		position: relative;
		margin-top: 3.5rem;
		&,
		&::before,
		&::after {
			width: 3rem;
			height: 2px;
			background-color: $color-grey-dark-3;
			display: inline-block;
		}
		&::before,
		&::after {
			content: '';
			position: absolute;
			left: 0;
			transition: all .2s;
		}
		&::before {top: -.8rem};
		&::after {top: .8rem};
	}

	&__button:hover &__icon::before {
        top: -1rem;
    }

    &__button:hover &__icon::after {
        top: 1rem;
    }

	// change menu icon after a click
	&__checkbox:checked + &__button &__icon {
        background-color: transparent;
    }

    &__checkbox:checked + &__button &__icon::before {
        top: 0;
        transform: rotate(135deg);
    }

    &__checkbox:checked + &__button &__icon::after {
        top: 0;
        transform: rotate(-135deg);
    }
}