
.heading-primary {
	color: $color-white;
	text-transform: uppercase;
	/* fix do zniwelowania skakania animacji */
	backface-visibility: hidden;	
	margin: 6rem;


	&--main {
		letter-spacing: 3.5rem;
		font-size: 6rem;
		font-weight: 400;
		display: block;
		animation-name: moveInLeft;
		animation-duration: 1s;
	
		@include respond(phone){
			letter-spacing: 1rem;
			font-size: 5rem;
		}
	}
	
	&--sub {
		display: block;
		letter-spacing: 1.74rem;
		text-transform: uppercase;
		font-size: 2rem;	
		font-weight: 700;
		animation-name: moveInRight;
		animation-duration: 1s;
		animation-timing-function: ease-out;
		
		@include respond(phone){
			font-size: 1.5rem;
			letter-spacing: .5rem;
			
		}
	}
	
}

body {
	font-family: 'Lato', sans-serif; 
	/* font-size: 16px; */
	font-weight: 400;
	line-height: 1.7;
	color: $color-grey-dark;
}

.heading-secondary{
	font-size: 3.5rem;
	text-transform: uppercase;
	font-weight: 700;
	// display inline-block - h2 no longer occupies the whole width of the section
	display: inline-block;
	// creating text with a gradient color style
	background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
	// background is directly behind text
	-webkit-background-clip: text;
	// color is transparent in order to uncover gradient that is behind it 
	color: transparent;
	letter-spacing: 0.2rem;
	transition: 0.2s;

	@include respond(tab-port){
		font-size: 3rem;
	}

	@include respond(phone){
		font-size: 2.5rem;
	}

	&:hover{
		transform: skewY(2deg) skewX(15deg) scale(1.1);
		text-shadow: .5rem 1rem 2rem  rgba($color-black, .2);
	}
}

.heading-tertiary {
	font-size: $default-font-size;
	font-weight: 700;
	text-transform: uppercase;
}

.paragraph {
	font-size: $default-font-size;
	
	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}